<template>
  <div class="house-fight">
    <div class="fight-box">
      <img v-if="round !== '0'" class='fight-bg' src='@/assets/images/fight-background.png' alt='fight-bg'>
      <div v-if="round !== '0'" class="fight-monster-power">
        <div>怪力值</div>
        <div>{{ monsterPower }}</div>
      </div>
      <img
        v-if="round !== '0'"
        class='fight-ling'
        :style="`z-index: ${isFight === 'lose' ? '0' : '3' }`"
        src='@/assets/images/ling-photo.png'
        alt='ling'
      >
      <img v-if="isShowReady === 'ready'" @click="fight" class='fight-ready' src='@/assets/images/ready-to-fight.png' alt='start'>
      <!-- 回合、血量 -->
      <div v-else-if="isShowReady === 'round'" class="fight-round-box">
        <div class="fight-round-text">ROUND {{ round }}</div>
        <div class="fight-circle-box">
          <div
            class="fight-circle"
            :style="`backgroundColor: ${roundWin.first ? '#95ff00' : 'none' }`"
          ></div>
          <div
            class="fight-circle"
            :style="`backgroundColor: ${roundWin.second ? '#95ff00' : 'none' }`"
          ></div>
          <div
            class="fight-circle"
            :style="`backgroundColor: ${roundWin.third ? '#95ff00' : 'none' }`"
          ></div>
        </div>
        <div class="fight-my-energy-box">
          <div class="fight-my-energy-text">怪力值</div>
          <div class="fight-my-energy">
            <div
              :style="`width: ${monsterUsedPower}%;`"
              class="fight-my-energy-inside"></div>
          </div>
        </div>
        <div class="fight-my-energy-box">
          <div class="fight-my-energy-text fight-greenColor">怪力值</div>
          <div class="fight-my-energy">
            <div
              :style="`width: ${usedPower}%;`"
              class="fight-my-energy-inside fight-greenBg"
            ></div>
          </div>
        </div>
        <div class="fight-second">{{ tenSecond }}</div>
      </div>
      <!-- 輸、獲勝顯示 -->
      <div v-else-if="isShowReady === 'lose'" class="fight-lose-text">任務失敗，請補充怪力值再回來吧！</div>
      <div v-else-if="isShowReady === 'win'" class="fight-win-display">
        <img class='fight-grey-man' src='@/assets/images/ling-card-grey.png' alt='ling-card-grey'>
        <div>獲得林阿群</div>
        <div class="fight-win-text">快去怪奇相簿看看你的收穫吧！</div>
      </div>
      <!-- 逃走、攻擊 -->
      <img v-if="isFight === 'escape'"
        @click="escape"
        class='fight-escape'
        src='@/assets/images/escape.png'
        alt='escape'
      >
      <img
        v-else-if="isFight === 'click'"
        @click="attack" class='fight-escape'
        src='@/assets/images/click.png'
        alt='click'
      >
      <div v-else-if="isFight === 'roundTwo'"
        @click="roundTwo"
        class="fight-round"
      >下一回</div>
      <div v-else-if="isFight === 'roundThree'"
        @click="roundThree"
        class="fight-round"
      >下一回</div>
      <div v-else-if="isFight === 'lose'"
      ></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'HauntedHouseFight',
  components: {

  },
  data () {
    return {
      isShowReady: 'ready',
      isFight: 'escape',
      usedPower: 0,
      monsterPower: 77,
      monsterUsedPower: 0,
      round: '1',
      tenSecond: 10,
      roundWin: {
        first: false,
        second: false,
        third: false,
        win: 0
      }
    }
  },
  mounted () {
  },
  methods: {
    fight () {
      this.isFight = 'click'
      this.isShowReady = 'round'
      this.roundOne()
    },
    escape () {
      this.isFight = 'escape'
      this.loseLing()
      this.$store.commit('changePoint', 'lingMap')
      this.$router.push({
        path: './monster-compass'
      })
    },
    roundOne () {
      // 開始倒數十秒
      let round1 = setInterval(() => {
        this.tenSecond--
      }, 1000)

      setTimeout(() => {
        this.monsterUsedPower = 11
      }, 3000)

      setTimeout(() => {
        this.monsterUsedPower = 17
      }, 6000)

      setTimeout(() => {
        this.monsterUsedPower = 29
      }, 9200)

      // 時間結束
      setTimeout(() => {
        clearInterval(round1)
        this.monsterPower = this.monsterPower - this.monsterUsedPower
        this.round = '2'
        this.isFight = 'roundTwo'
        if (this.usedPower > this.monsterUsedPower) {
          this.roundWin.first = true
          this.roundWin.win++
        } else {

        }
      }, 10000)
    },
    roundTwo () {
      // 重置
      this.usedPower = 0
      this.monsterUsedPower = 0
      this.tenSecond = 10
      this.isFight = 'click'

      // 開始倒數十秒
      let round2 = setInterval(() => {
        this.tenSecond--
      }, 1000)

      setTimeout(() => {
        this.monsterUsedPower = 11
      }, 3000)

      setTimeout(() => {
        this.monsterUsedPower = 17
      }, 6000)

      setTimeout(() => {
        this.monsterUsedPower = 29
      }, 9200)

      // 時間結束
      setTimeout(() => {
        clearInterval(round2)
        this.monsterPower = this.monsterPower - this.monsterUsedPower
        this.round = '3'
        this.isFight = 'roundThree'
        if (this.usedPower > this.monsterUsedPower) {
          this.roundWin.second = true
          this.roundWin.win++
        } else {

        }
        if (this.roundWin.win === 2) {
          alert('你打贏林阿群了！')
          this.winLing()
        } else if (this.roundWin.win === 0) {
          alert('你輸了！')
          this.loseLing()
        }
      }, 10000)
    },
    roundThree () {
      // 重置
      this.usedPower = 0
      this.monsterUsedPower = 0
      this.tenSecond = 10
      this.isFight = 'click'

      // 開始倒數十秒
      let round3 = setInterval(() => {
        this.tenSecond--
      }, 1000)

      setTimeout(() => {
        this.monsterUsedPower = 11
      }, 3000)

      setTimeout(() => {
        this.monsterUsedPower = 17
      }, 6000)

      setTimeout(() => {
        this.monsterUsedPower = 19
      }, 9200)

      // 時間結束
      setTimeout(() => {
        clearInterval(round3)
        this.monsterPower = this.monsterPower - this.monsterUsedPower
        if (this.usedPower > this.monsterUsedPower) {
          this.roundWin.third = true
          this.roundWin.win++
        } else {

        }
        if (this.roundWin.win === 2) {
          alert('你打贏林阿群了！')
          this.winLing()
        } else if (this.roundWin.win === 0) {
          alert('你輸了！')
          this.loseLing()
        }
      }, 10000)
    },
    attack () {
      if (this.power > 0) {
        this.$store.commit('minusPower')
        this.usedPower++
      }
    },
    winLing () {
      this.isFight = 'win'
      this.isShowReady = 'win'
      this.round = '0'
      this.$store.commit('fightLing', 'win')
      // 取得卡片
      this.$store.commit('shakeHeader', 'collect')
      setTimeout(() => {
        this.$store.commit('shakeHeader', '')
      }, 1000)
      this.$store.commit('getLingCard', 'greyLingCard')
    },
    loseLing () {
      this.isFight = 'lose'
      this.isShowReady = 'lose'
      this.round = '0'
      this.$store.commit('fightLing', 'lose')
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>

  .house-fight {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .fight {

      &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: fixed;
        top: 0px;
        max-width: 475px;
        z-index: 2;
      }

      &-monster-power {
        position: fixed;
        top: 20px;
        left: 66%;
        color: #fff;
        background-color: #a785b7;
        text-align: center;
        padding: 10px;
        font-size: 18px;
        border-radius: 20px;
        z-index: 3;
      }

      &-ling {
        position: fixed;
        top: 0px;
        width: 320px;
        z-index: 3;
      }

      &-ready {
        position: fixed;
        top: 320px;
        width: 200px;
        z-index: 4;
      }

      // 回合

      &-round-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 200px;
        z-index: 4;
      }

      &-round-text {
        font-size: 30px;
        color:  #95ff00;
      }

      &-circle-box {
        display: flex;
        margin-top: 10px;
      }

      &-circle {
        width: 15px;
        height: 15px;
        border-radius: 100%;
        border: 0.5px solid #95ff00;
        margin: 0px 5px;
      }

      // 血量

      &-my-energy-box {
        display: flex;
        margin-top: 10px;
      }

      &-my-energy-text {
        background: #000;
        border: 2px solid #000;
        width: 48px;
        height: 30px;
        color: #a785b7;
        font-size: 12px;
        text-align: center;
        line-height: 26px;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
      }

      &-my-energy {
        border: 2px solid #000;
        height: 30px;
        width: 250px;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        background: #000;
      }

      &-my-energy-inside {
        height: 100%;
        background: #a785b7;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
      }

      &-greenColor {
        color:  #95ff00!important;
      }

      &-greenBg {
        background-color:  #95ff00;
      }

      // 回合、血量 end

      &-second {
        color: #fff;
        margin-top: 10px;
        font-size: 20px;
      }

      &-lose-text {
        margin-top: 320px;
        color: #95ff00;
      }

      &-win-display {
        text-align: center;
        color: #fff;
        z-index: 3;
      }

      &-grey-man {
        width: 320px;
      }

      &-win-text {
        margin-top: 20px;
        color: #95ff00;
      }

      &-escape {
        position: fixed;
        bottom: 30px;
        right: 60%;
        width: 100px;
        z-index: 4;
        touch-action: manipulation;
      }

      &-round {
        position: fixed;
        bottom: 30px;
        right: 60%;
        width: 80px;
        height: 80px;
        line-height: 80px;
        color: #fff;
        text-align: center;
        background: #000;
        border-radius: 100%;
        z-index: 4;
      }
    }
  }

</style>



